* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list{
  display: flex;
  flex-direction: column;
  gap: 8px;
  padding: 0;
  list-style: none;
}
.country-list span{
  display: inline-block;
  margin-left: 10px;
  font-size: 18px;
}
.country-info{
  display: flex;
  flex-direction: column;
  gap: 10px;

  margin-left: 10px;
  font-size: 18px;

}
.country-header{
  display: flex;
  gap: 15px;
}

h1{
  font-weight: 500;
  font-size: 28px;
  margin: 0;
}
p{
  margin: 0;
}